/**
 * Name: BizAgi Desktop Widget Dashboard menu (left sidebar) * 
 * @author Valeria Vanegas
 */


bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.project.dashboard.menu", {}, {

  /**
                                                                                                   * Handler when discussions menu status change
                                                                                                   * @param {*} ev 
                                                                                                   * @param {*} params 
                                                                                                   */
  onDiscussionsMenuStatusChange: function onDiscussionsMenuStatusChange(ev, params) {
    var self = this;
    self.updateDiscussionsIcons(params.hasDiscussionsOrComment);
  },

  /**
      * Update discussions icon on sidebar menu
      * The icon is different when there is other discussion or there are comments
      * @param {*} hasDiscussionsOrComment 
      */
  updateDiscussionsIcons: function updateDiscussionsIcons(hasDiscussionsOrComment) {
    var self = this;

    self.sub("UPDATE_DISCUSSIONS_MENU_STATUS", $.proxy(self.onDiscussionsMenuStatusChange, self));

    $(".bz-icon-discussion-comment").toggle(hasDiscussionsOrComment);
    $(".bz-icon-discussion-no-comment").toggle(!hasDiscussionsOrComment);
    self.params.hasDiscussionsOrComment = hasDiscussionsOrComment;
  },

  /**
      * Detach events
      */
  clean: function clean() {
    var self = this;
    self.unsub("UPDATE_DISCUSSIONS_MENU_STATUS", $.proxy(self.onDiscussionsMenuStatusChange, self));
  } });


bizagi.injector.register("bizagi.workportal.widgets.project.dashboard.menu", ["workportalFacade", "dataService", bizagi.workportal.widgets.project.dashboard.menu], true);